<template>
  <div class="center_map">
    <div id="map"></div>
    <div class="my_control">
      <el-tooltip
        class="item"
        effect="dark"
        content="获取当前定位, 需要授权获取权限"
        placement="top"
      >
        <i class="el-icon-location" style="color: #fff; font-size: 24px"></i>
      </el-tooltip>

    <i class="el-icon-location" style="color: #fff"></i>
      <i class="el-icon-user-solid" style="color: #fff"></i>
    </div>
    <!-- <div class="input-item">
      <el-input v-model="input" placeholder="请输入内容"></el-input>
    </div> -->
  </div>
</template>

<script>
import AMapLoader from "@amap/amap-jsapi-loader";
export default {
  name: "HomeMap",
  data() {
    return {
      description: "地图",
      GD: null,
      map: null,
      input: "经纬度坐标",
    };
  },
  mounted() {
    this.initGD();
  },
  unmounted() {
    this.map?.destroy();
  },
  methods: {
    initGD() {
      AMapLoader.load({
        key: "9fc43661063065e634b3b9bcba8bd7c7", // 申请好的Web端开发者Key，首次调用 load 时必填
        version: "2.0", // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
        plugins: [
          "AMap.ToolBar", //工具条
          "AMap.Scale", // 比例尺
          "AMap.Geolocation", //定位
          "AMap.HawkEye", //鹰眼，显示缩略图
          "AMap.MapType", //图层切换，用于几个常用图层切换显示
        ], //需要使用的的插件列表，如比例尺'AMap.Scale'，支持添加多个如：['...','...']
      })
        .then((AMap) => {
          this.GD = AMap;
          this.initMap();
          this.initControl();
          this.getPoint();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    initMap() {
      // 设置地图容器id
      this.map = new this.GD.Map("map", {
        mapStyle: "amap://styles/darkblue", //设置地图的显示样式
        viewMode: "3D", // 是否为3D地图模式
        zoom: 4.8, // 初始化地图级别,
        rotateEnable: true,
        pitchEnable: true,
        // center: [116.397428, 39.90923], // 初始化地图中心点位置
        center: [103.397428, 35.90923], // 初始化地图中心点位置
        features: ["bg", "road", "building", "point"], // 地图显示要素
      });
      //使用CSS默认样式定义地图上的鼠标样式
      this.map.setDefaultCursor("pointer");
    },
    initControl() {
      //在地图上显示工具条方法
      this.map.addControl(
        new this.GD.ToolBar({
          //toolBar
          // position: 'RB', // 默认位置：LT:左上角;
          offset: [18, 60],
          autoPosition: true, //是否自动定位  默认为false
          locate: false, //是否显示定位按钮，默认为 true
        })
      ); // 工具条
      this.map.addControl(new this.GD.Scale()); // 比例尺
      // this.map.addControl(new this.GD.HawkEye({
      //   showButton: true, // 是否显示打开关闭的按钮
      //   mapStyle: 'amap://styles/darkblue' //缩略图要显示的地图自定义样式，如'amap://styles/dark'
      // })); // 鹰眼  https://lbs.amap.com/api/javascript-api-v2/documentation#hawkeye
      // this.map.addControl(new this.GD.MapType()); // 图层切换
      this.geolocation = new this.GD.Geolocation({
        //定位
        enableHighAccuracy: true, //是否使用高精度定位，默认:true
        convert: true, //自动偏移坐标 为高德坐标，默认：true
        timeout: 5000, //超过5秒后停止定位，默认：无穷大
        buttonPosition: "RB", //定位按钮停靠位置
        showMarker: false, //定位成功在位置显示点标记，默认：true
      });
      // this.map.addControl(this.geolocation); //在地图上引入定位插件
    },
    getPoint() {
      //为地图注册click事件获取鼠标点击出的经纬度坐标
      this.map.on("click", (e) => {
        this.input = e.lnglat.getLng() + "," + e.lnglat.getLat();
      });
    },
  },
};
</script>
<style lang="less" scoped>
.center_map {
  position: relative;
  padding: 5px;
  #map {
    width: 100%;
    height: calc(100vh - 110px);
    // background: red;
  }
  .my_control {
    position: absolute;
    bottom: 2px;
    left: 50%;
    z-index: 999;
    transform: translate(-50%);
    width: 500px;
    height: 40px;
    border: 1px solid #2761a5;
    border-bottom: none;
    border-radius: 5px 5px 0 0;
  }
